.seccion-redes {
  background-color: #0fbeb1;
  width: auto;
  height: auto;
}
.titulo-redes{
    color:white;
    padding: 50px;
    font-size: 60px;
}
.ico-redes {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  grid-auto-rows: minmax(100px, auto);
  width: 70%;
  padding: 50px 0;
}

.logos-img {
  height: 120px;
}

.se√±alar {
  height: 400px;
}

.uno {
  grid-column: 1;
  grid-row: 1;
}
.dos {
  grid-column: 3;
  grid-row: 1;
}
.tres {
  grid-column: 5;
  grid-row: 1;
}
.cuatro {
  grid-column: 2;
  grid-row: 3;
}
.cinco {
  grid-column: 4;
  grid-row: 3;
}
.seis {
  grid-column: 6;
  grid-row: 3;
}
