.secciones-port{
    background-color: #F3F3F3;
    width: 80%;
    height: 150px;
    border-radius: 15px;
    display: flex;
    align-items:  center;
    margin: 30px 0;
    justify-content: space-between;
}
.logo-secciones{
    max-width: 120px;
    max-height: 120px;
    width: 120px;
    padding: 15px 30px 15px 40px;
}
.logo-seccion-alura{
    padding: 15px 30px 15px 70px;
}
.link-secciones{
 color: white;
 font-size: 25px;
}
.boton-secciones, .boton-seccion-alura{
    background-color: #04656E;
    height: 50%;
    width: 60%;
    border-radius: 20px;
    border-style: none;
    margin-right: 40px;
}