.seccion-contacto{
    background-color: #04656E;
    height: auto;
}
.contacto-items{
    width: 70%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.astronauta-contacto{
    height: 400px;
}
.formulario{
    margin: 80px 0 50px 0;
    height: 400px;
    width: 800px;
    background-color: #13A79B;
    border-radius: 25px;
    border :10px #0FBEB1 solid;
}

.titulo-contacto{
    color: white;
    font-size: 50px;
    margin: 10px 0;
}
.form{
    display: grid;
    justify-content: center;
    justify-items: center;
}
form input, form textarea{
    border-radius: 15px;
    border: 5px #04656E solid;
    margin: 10px;
    height: 30px;
    width: 400px;
}
form input::placeholder, form textarea::placeholder{
    padding-left: 10px;
    font-weight: bold;
    font-size: 1.3em;
    color: #13A79B;
}
form textarea{
    height: 100px;
}
.boton-contacto{
    background-color: #0FBEB1;
    border: 3px #04656E solid;
    border-radius: 10px;
    margin: 10px;
    width: 100px;
    color: white;
}
.boton-contacto:hover{
    background-color: #13A79B;
    border: 3px #04656E solid;
}
.boton-contacto:active{
    background-color: #04656E;
    border: 3px #13A79B solid;
}